body {
    margin : 0;
    padding : 0;
    font-family: "Karla";
    color : #ffffffb6;
    background-color : #4C8577;
    line-height: 1.5rem;
    height : 100%;
}

html{
    height : 100%;
    width : 100%;
    position: fixed;
}

#root {
    height : 100%;
    width : 100%;
}

@media (max-width : 975px){
    html{
        position: relative;
    }
}
@media(max-width : 520px){
    html{
        position: relative;
    }
}